








import { Component, Vue } from "vue-property-decorator";
import NavBar from "@/components/NavBar.vue";

@Component({
  components: {
    NavBar
  }
})
export default class App extends Vue {
  constructor() {
    super();
  }

  private home() {
    if (this.$route.path !== "/conta") {
      return true;
    } else {
      return false;
    }
  }
}
